import puterService from "./puterService";

/**
 * AI Service for TrendyAI Integration
 * Handles all AI-powered features including tutoring, recommendations, and analytics
 * Re-routed to use Puter.js for maximum reliability and zero-backend dependency
 */
class AIService {
    constructor() {
        this.currentSession = null;
        this.isInitialized = false;
    }

    static getInstance() {
        if (!AIService.instance) {
            AIService.instance = new AIService();
        }
        return AIService.instance;
    }

    async ensureInitialized() {
        if (!this.isInitialized) {
            this.isInitialized = await puterService.initialize();
        }
    }

    /**
     * Start a new AI tutor session
     */
    async startTutorSession(context) {
        try {
            await this.ensureInitialized();
            this.currentSession = {
                id: `session-${Date.now()}`,
                startTime: new Date(),
                context
            };
            return this.currentSession;
        }
        catch (error) {
            console.error('Start tutor session error:', error);
            throw new Error('Failed to initialize AI Tutor. Please try again later.');
        }
    }

    /**
     * Send a message to the AI tutor
     */
    async sendMessage(sessionId, message, context) {
        try {
            await this.ensureInitialized();

            // Build a specialized prompt for the Tutor if it's in a lesson context
            const history = context?.history || [];
            const specializedHistory = [
                {
                    role: 'system', content: `You are an expert tutor for Trendtactics Academy. 
                  Currently helping with course: ${context?.courseId || 'General Marketing'}.
                  Topic: ${context?.lessonId || 'General'}.`
                },
                ...history
            ];

            const responseText = await puterService.processConversation(specializedHistory, message);

            return {
                content: responseText,
                timestamp: new Date()
            };
        }
        catch (error) {
            console.error('Send AI message error:', error);
            throw new Error('AI is temporarily unavailable. Using offline assistant mode.');
        }
    }

    /**
     * Get AI-powered learning recommendations
     */
    async getRecommendations(context) {
        try {
            await this.ensureInitialized();
            // Simple mock for now but powered by Puter
            return [
                { id: 1, title: 'Mastering Facebook Ads', reason: 'Based on your interest in social media' },
                { id: 2, title: 'SEO for Beginners', reason: 'Highly relevant to your current track' }
            ];
        }
        catch (error) {
            return [];
        }
    }

    /**
     * Get AI analytics for user learning patterns
     */
    async getAnalytics(userId) {
        // Return structured mock data to prevent UI crashes
        return {
            totalSessions: 12,
            averageSessionDuration: 15,
            learningProgress: {
                completedLessons: 5,
                totalLessons: 20,
                averageScore: 85
            },
            engagementMetrics: {
                questionsAsked: 42,
                resourcesShared: 8
            },
            topTopics: ['Facebook Ads', 'Email Marketing', 'Copywriting']
        };
    }

    /**
     * Simplified progress analysis
     */
    async analyzeProgress(context) {
        return {
            summary: "You are doing great! Focus more on SEO modules next week.",
            nextSteps: ["Complete Chapter 3", "Take the SEO Quiz"]
        };
    }

    /**
     * Get current tutor session
     */
    getCurrentSession() {
        return this.currentSession;
    }

    /**
     * Clear current session
     */
    clearSession() {
        this.currentSession = null;
    }
}

// Export singleton instance
export const aiService = AIService.getInstance();
export default aiService;
